html{font-size: 100%;}
.containerSocial{
	margin: 1.5rem 1rem;
}
.aSocial{
	margin: 1.5rem 1rem;
}
.aSocial:hover{
	opacity: 0.5;
}

@media only screen and (min-device-width: 280px)and (max-device-width: 767px) {
  html{font-size:1rem;}
  .containerSocial{margin-top: 5rem;}
}




@media only screen and (min-device-width:768px)and (max-device-width: 1365px){
  html{font-size:1.2rem}
  .containerSocial{margin-top: 5rem;}
}


