body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: white;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
img{
  cursor: pointer;
}
.container{
  font-size: 100%;
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  
  /*-webkit-animation: fade-in 1.2s cubic-bezier(0.390, 0.575, 0.565, 1.000) 2s both;
          animation: fade-in 1.2s cubic-bezier(0.390, 0.575, 0.565, 1.000) 2s both;*/
}
.backButton{
  background-color: black;
  font-size: 1.5rem;
  width: 6rem;
  border-radius: 999px;
  margin-top: 2rem;
  border: 1px solid gray;
}
.backButton:hover{
  opacity: 0.5;
}
.backLink{
  background-color: black;
  color: white;
  text-decoration: none;
}


.social{width: 100%; text-align: center;}

/*////////////////////*/
.header{
  font-size: 3rem;
  font-style: italic;
  font-weight: 400;
  align-self: left;
  width: 100%;

}
.linkImg .tooltiptext {
  visibility: hidden;
   position: absolute;
  z-index: 1;
  margin-top: 15rem;
  width: 100%;
  left: 0rem;
  color:white;
  background-color: rgba(0, 0, 0, 0.5);
  font-size: 1.2rem;
  line-height: 0.4;
    padding: 1rem 0.5rem;
  /*padding: 0 1rem;*/
}

.searchImg{width: 15rem; height: 15rem;}
.linkImg:hover .tooltiptext {visibility: visible;}
@media only screen and (min-device-width: 280px)and (max-device-width: 767px) {
  html{font-size:1rem;}
  body{font-size: 1rem;}
  h2{font-size: 2rem;}
  h3{font-size: 1.5rem;}
  h4{font-size: 1rem;}
  .aboutContainer{width: 100%; margin: 0 0.5rem; }
  .aboutContainer p{font-size: 1rem;padding: 0 1rem;}
  
}




@media only screen and (min-device-width:768px)and (max-device-width: 1365px){
  html{font-size:1.2rem}
}





@media only screen and (min-device-width:1900px) {
  html{font-size: 1.5rem;}
}