.containerInfo{
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	width: 100%;
}
.box_Info{
	list-style-type: none;
	margin: 3rem 3rem;
	cursor: pointer;
}
.aInfo{
	text-decoration: none;
	color: white;
	font-size: 1.1rem;
	padding: 0rem 2rem;
}
.box_Info:hover{
    background-color: rgba(250, 250, 250, 0.2);
    border-radius: 999px;
}


@media only screen and (min-device-width: 280px)and (max-device-width: 767px) {
	.box_Info{margin: 4rem 0; }
	.aInfo{padding: 0 0.5rem;}
	
}
@media only screen and (min-device-width:768px)and (max-device-width: 1365px){
	.box_Info{margin: 4rem 0; }
}