.navul {
  width: 72rem;
  list-style-type: none;
  margin: 1rem 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  height: 5rem;
  margin-left: 0rem;
}

.navul li {

  padding: 0 6rem;
  position: relative;
  top: -1rem;
  cursor: pointer;
}

li .aNav {
  display: block;
  color: white;
  text-align: center;
  text-decoration: none;
  font-size:1.2rem;
  width: 3.9rem;
 
  position: absolute;
  z-index: 1001;
}

/*li a:hover {
  background-color: #111;
}*/
.aNav::after {    
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 1px;
  left: 50%;
  position: relative;
  background: #fff;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}
 .aNav:hover::after { 
  width: 100%; 
  left: 0; 
}

