.containerBag{
  background-color: white;
  color: black;
  width: 100%;
  padding:  4rem 0;
  font-size: 1.2rem;

}
hr{border-top: 1px dashed black;}
table{
  width: 80%;
  margin-right: 1rem;


}
.smallbox{
    margin-top: 5rem;

}
.total{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  font-weight: 400;
  font-size: 1.4rem;
   background-color: rgba(0, 0, 0, 0.5);
  color: white;



}
.buy{
  width: 15rem;
  height: 2rem;
  border-radius: 999px;
  margin: 3rem 0;
  font-weight: 400;
  font-size: 1.2rem;
}
.buy:hover{
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.2);



}