.containerImg{
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  justify-content: space-evenly;

}
.boxImg{
  padding: 2rem 0rem;
}
.boxImg a{margin-top: 1.5rem;}
.boxImg a:hover{cursor: pointer;transform: scale(1.1);}
.linkImg {position: relative;}
.linkImg .tooltiptext {
  visibility: hidden;
   position: absolute;
  z-index: 1;
  margin-top: 15rem;
  width: 100%;
  left: 0rem;
  color:white;
  background-color: rgba(0, 0, 0, 0.5);
  font-size: 1.2rem;
  line-height: 0.4;
    padding: 1rem 0.5rem;
  /*padding: 0 1rem;*/
}
.linkImg:hover .tooltiptext {visibility: visible;}
