  color:white;
  background-color: black;
  border-radius: 20px;
  font-size: 1.5rem;
  padding: 0 1rem;
}*/
.img{position: relative;z-index: 0;}
.tooltiptext{
  text-align: left;
}


@media only screen and (min-device-width: 280px)and (max-device-width: 767px) {
  html{font-size:1rem;}
  img{width: 9rem; height: 9rem;}
  .linkImg .tooltiptext{ color: rgba(108, 122, 137, 1);visibility: visible; margin-top: 1rem; line-height: unset; padding: 0; font-size: 0.9rem;}
  .tooltiptext p{color: white;}
}




@media only screen and (min-device-width:768px)and (max-device-width: 1365px){
  html{font-size:1.2rem}
  .linkImg .tooltiptext{ color: rgba(108, 122, 137, 1);visibility: visible; margin-top: 1rem; line-height: unset; padding: 0; font-size: 0.9rem;}
  .tooltiptext p{color: white;}
}





@media only screen and (min-device-width:1900px) {
  html{font-size: 1.5rem;}
}