html{font-size: 100%;}


.rc-menu__item--hover {
     background-color: none; 
}
.rc-menu--animation .rc-menu_item{
  transition-property: none;
}
.rc-menu-button {
  background-color: black; 
  border: black;


}
.rc-menu-container ul{
 background-color: rgba(108, 122, 137, 0.95);
 /*color: white;*/
}

.rc-menu{

  height: auto;
}
.rc-menu_submenu{
  padding: 0 1rem;
}
.rc-menu__submenu > .rc-menu__item{
  font-size: 1.5rem;
  color: black;
}
/*.rc-menu__submenu > .rc-menu__item:hover{
  color: black;
}*/
.rc-menu--open.rc-menu--dir-right{
  overflow: visible;
  top: -0.5rem;
}
.rc-menu-container ul{
  left: -78px;
  top: 100px;
}
.rc-menu-container ul li{
  padding: 0.375rem 0;
   height: 2rem;

}
/*.rc-menu-container li div:hover{
  background-color: black;

}*/
/*.rc-menu-container li:hover{
  background-color: black;

}*/
.rc-menu-container ul li p{
  padding: 0rem 1rem;
}
.rc-menu_item{
  float: unset;

  
}

.aMenu{
  color: black;
  text-decoration: none;
  font-size: 1.3rem;
}

.navbar {
  width: 3rem;
  display: block;
  margin: 1rem 2.5rem;
  padding: 0 0.5rem;
 /* transition: all 0.5s;
  -webkit-transition: all 0.5s;
*/
}
.navbar:hover{
  cursor: pointer;

}
.navbar div {
  width: 2.5rem;
  height: 5px !important;
  background-color: white;
  margin: 7px 0px 7px 0px;
  border-radius: 25px;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
}
.one, .two, .three{
  width: 2.5rem;
}
.navbar:hover .one{
  background-color: grey;
}
.navbar:hover .two{
  background-color: grey;
}
.navbar:hover .three{
  background-color: grey;
}
