  width: 2.5rem;
}
*/
@media only screen and (min-device-width: 280px)and (max-device-width: 767px) {
  html{font-size:1rem;}
  .rc-menu{z-index: 1002; min-height: 15rem;}
  .rc-menu-button {margin-right: 19rem;}
  .rc-menu-container ul{background-color: rgba(108, 122, 137, 1);}

}




@media only screen and (min-device-width:768px)and (max-device-width: 1365px){
  html{font-size:1.2rem}
  .rc-menu{z-index: 1002;}
  .rc-menu-button {margin-right: 30rem; position: relative; left: -3rem;}
.rc-menu-container ul{background-color: rgba(108, 122, 137, 1);}
}





@media only screen and (min-device-width:1900px) {
  html{font-size: 1.5rem;}
  .rc-menu-button{ left: 0; position: absolute; top: 1rem;}
}