html{font-size: 100%;}
@import url('https://fonts.googleapis.com/css2?family=Style+Script&display=swap');

.navul {
  width: 72rem;
  list-style-type: none;
  margin: 1rem 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  height: 5rem;
  margin-left: 0rem;
}

.navul li {

  padding: 0 6rem;
  position: relative;
  top: -1rem;
  cursor: pointer;
}

li .aNav {
  display: block;
  color: white;
  text-align: center;
  text-decoration: none;
  font-size:1.2rem;
  width: 3.9rem;
 
  position: absolute;
  z-index: 1001;
}

/*li a:hover {
  background-color: #111;
}*/
.aNav::after {    
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 1px;
  left: 50%;
  position: relative;
  background: #fff;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}
 .aNav:hover::after { 
  width: 100%; 
  left: 0; 
}

.boutique{
  font-family: 'Style Script', cursive;
  font-size: 2.5rem;
  text-decoration: underline;
  color: white;
  padding: 0rem 0;
  margin-bottom: 0;

}
.boutique:hover{
  color: black;

}
.fifth{
  padding-right: 3.5rem;
}
.sixth{
  display: flex;
  width: min-content;
  position: absolute;
  left:  85rem;
  top: -1.8rem;
  margin-top: 1.5rem;
  cursor: pointer;

}
.sixth p{
  background-color: rgba(250, 250, 250,0.6);
  padding: 0.5rem 1rem;
  border-radius: 10px;

}
.divBag{
  margin-top: 0.5rem;
}
.divBag:hover{
  opacity: 0.5;

}
.bag{
   width: 2rem;
}
.fa-stack[data-count]::before {
  position: relative;
  top: 0.5rem;
  right: -1rem;
  content: attr(data-count);
  font-size: 60%;
  padding: 0.6em;
  line-height: 0.75em;
 
  color: black;
  text-align: center;
  min-width: 2em;
  font-weight: bold;
  background: white;
  border-radius: 999px;
}
/*.fa-stack:hover:after{
  width: 0;
}
*/
.robotinput{
  height: 2rem;
  width: 18rem;
  font-size: 1rem;
}
.containerBag{
  background-color: white;
  color: black;
  width: 100%;
  padding:  4rem 0;
  font-size: 1.2rem;

}
hr{border-top: 1px dashed black;}
table{
  width: 80%;
  margin-right: 1rem;


}
.smallbox{
    margin-top: 5rem;

}
.total{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  font-weight: 400;
  font-size: 1.4rem;
   background-color: rgba(0, 0, 0, 0.5);
  color: white;



}
.buy{
  width: 15rem;
  height: 2rem;
  border-radius: 999px;
  margin: 3rem 0;
  font-weight: 400;
  font-size: 1.2rem;
}
.buy:hover{
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.2);



}
@media only screen and (min-device-width: 280px)and (max-device-width: 767px) {
  html{font-size:1rem;}
  .containerNav{display: flex; justify-content: center;}
  li .aNav{font-size: 1.2rem;width: unset; z-index: unset;}
  
  .boutique{margin-left: 0;}
  .first{order: 4; margin: 1rem 0rem 1.5rem 0rem; left: -2rem; }
 
  .second{order: 3;margin: 1rem 0rem 1.5rem 0rem; right: 2rem;}
  .third{order: 1;padding: 0 0; margin: 0; width: 100%;}
  .forth{order: 5;margin: 1rem 0rem 1.5rem 0rem; right: 1rem;}
  .fifth{order: 2;padding: 0rem 4rem 0rem 0rem; margin: 1rem 0rem 1.5rem 0rem; left: -2rem;}
 
  .sixth{width: 0.1rem; left: 19rem; top: -2.8rem;}
  .sixth p{padding: 0.1rem 0.5rem;}
  .navul{width:23rem; background: unset; flex-wrap: wrap; justify-content: space-evenly;    padding: 0;position: unset; z-index: unset; margin-left: 0; height: auto;}
  .bag{width: 2rem; height: 2rem;}
  .fa-stack[data-count]:before{right: -0.5rem; font-size: 40%;}
 .navul li {padding: 0 4rem;/*padding: 0rem 4rem 0rem 0rem;*/ /*margin: 1rem 8rem 1.5rem 8rem; */}
/* .navul li p {text-decoration: underline;}*/
}




