.containerDetail{


}
.boxDetail{
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 4rem;



}
.bigimg{
	padding: 1rem 1.5rem;
	margin-top:3rem;
	margin-bottom: 7rem;

}
.extras{
	display: flex;
	/*flex-wrap: wrap;*/
	justify-content: center;
	margin: 3rem 0;


}
.color{
	display: flex;
	justify-content: center;

}
.col{
	font-size: 1.2rem;
}
.color p{
	margin: 1rem 2rem;
	font-weight: bolder;
}
.color div{
	border: 1px solid white;
	width: 3rem;
	height: 3rem;
	cursor: pointer;
}
.color div:hover{
	opacity: 0.5
}
.grey{
	background-color: grey;
}
.white{background-color: white;}
.khaki{background-color: #013220;}
.bronze{background-color:#cd7f32;}
.size{
	display: flex;
	justify-content: center;
	margin-bottom: 3rem;
}
.siz{
	font-size: 1.2rem;
}

.size p{
	margin: 1rem 1rem;
	padding: 0 0.5rem;

	font-weight: bolder;

}
.size div{
	border: 1px solid white;
	margin: 0 1rem;
	cursor: pointer;
}
.size div:hover{
	color: black;
	background-color: rgba(250, 250,250, 0.8)
}
.add{
	margin-bottom: 2rem;
}
.add button{
	background-color: rgba(0, 0, 0, 0.8);
	color: white;
	border: 1px solid white;
	border-radius: 999px;
	font-size: 1.5rem;
	padding: 0.5rem 2.5rem;
	margin: 2rem 0;
	box-shadow: 0.2rem 0.2rem 0 rgba(250, 250,250, 0.2)
}
.add button:hover{
	color: black;
	background-color: white;
	cursor: pointer;

}
hr{
	margin-bottom: 4rem;
}

@media only screen and (min-device-width: 280px)and (max-device-width: 767px) {
	html{font-size:1rem;}
	.boxDetail{justify-content: center;}
	.bigimg{margin-bottom: 1rem; margin-top: 0; width: 15rem; height: 15rem;}
	.middleImg{height: 7rem;}
	.size{flex-wrap: wrap;}
}

@media only screen and (min-device-width:768px)and (max-device-width: 1365px) {
	 html{font-size:1.2rem}
	.boxDetail{justify-content: center;}
	.bigimg{margin-bottom: 1rem; margin-top: 0; }
}