.backBtn{
	width: 8rem;
	font-size: 1.2rem;
	background-color: black;
    color: white;
    border-radius: 999px;
    margin: 3rem 0;
}
.backBtn:hover{
	opacity: 0.5;
	cursor: pointer;
}
h2{
	font-size: 6rem;
	text-align: left;
	margin: 3rem 0 0 9rem;
}
h3{
	font-size: 4rem;
	text-align: left;
	margin: 3rem 0 0 9rem;
}
hr{margin: 0 9rem;}
.aboutContainer {
	display: inline-block;
	float: left;
	width: 60%;
	margin: 0 9rem;
}
h4{
	font-size: 2rem;
	text-align: left;
	text-decoration: overline;
}

.aboutContainer p{
	font-size: 1.5rem;
	text-align: left;
}
.table2{
	margin-bottom: 15rem;
}
table, td, th{
	border: 1px solid white;
}
@media only screen and (min-device-width: 280px)and (max-device-width: 767px) {
  html{font-size:1rem;}
  
 }

 @media only screen and (min-device-width:768px)and (max-device-width: 1365px){
  html{font-size:1.2rem}
}
